<template>
	<button
		:active="active"
		:class="[colors[color]]"
		class="button"
		v-on="$listeners"
	>
		<slot />
	</button>
</template>

<script>
export default {
	props: {
		active: {
			type: Boolean,
			default: false,
		},
		color: {
			type: String,
			default: 'transparent',
		},
	},
	data() {
		return {
			colors: {
				transparent: 'button__transparent',
				white: 'button__white',
				gray: 'button__gray',
			},
		};
	},
};
</script>

<style lang="scss" scoped>
.button {
	height: 36px;
	min-width: 36px;
	padding: 0 18px;
	margin-right: 3px;
	font-size: 0.8rem;
	border-radius: 6px;
	cursor: pointer;

	&__transparent {
		background-color: transparent;
		color: white;

		&:hover {
			background-color: rgba(255, 255, 255, 0.15);
		}

		&:active {
			background-color: rgba(255, 255, 255, 0.3);
		}

		&[active] {
			background-color: rgba(255, 255, 255, 0.2);
		}
	}

	&__white {
		font-weight: 600;
		background-color: rgba(255, 255, 255, 0.1);
		color: white;

		&:hover {
			background-color: rgba(255, 255, 255, 0.15);
		}
		&:active {
			background-color: rgba(255, 255, 255, 0.2);
		}

		&[active] {
			background-color: white;
			color: #80808f;
		}
	}

	&__gray {
		background-color: transparent;
		color: #000000;

		&:hover {
			background-color: rgba(0, 0, 0, 0.15);
		}

		&:active {
			background-color: rgba(0, 0, 0, 0.2);
		}

		&[active] {
			background-color: #464e5f;
			color: white;
		}
	}
}
</style>
